import BackgroundSection from "components/BackgroundSection/BackgroundSection";
import ListingImageGallery from "components/ListingImageGallery/ListingImageGallery";
import { ListingGalleryImage } from "components/ListingImageGallery/utils/types";
import SectionSliderNewCategories from "components/SectionSliderNewCategories/SectionSliderNewCategories";
import SectionSubscribe2 from "components/SectionSubscribe2/SectionSubscribe2";
import { Tour } from "data/types";
import { useGetTourByIdMutation } from "features/toursApiSlice";
import { ReactNode, useEffect, useState } from "react";
import { useLocation, useNavigate, useParams, useSearchParams } from "react-router-dom";
import MobileFooterSticky from "./(components)/MobileFooterSticky";


export interface Image {
  id:          number;
  tour_images: string;
  tour_id:     number;
}

const DetailPagetLayout = ({ children }: { children: ReactNode }) => {
  const navigate = useNavigate();
  const thisPathname = useLocation().pathname;
  const [searchParams] = useSearchParams();
  const modal = searchParams?.get("modal");

  const handleCloseModalImageGallery = () => {
    let params = new URLSearchParams(document.location.search);
    params.delete("modal");
    navigate(`${thisPathname}/?${params.toString()}`);
  };

  const { id } = useParams();

  const [tour, setTour] = useState({} as Tour);

  const [getTour] = useGetTourByIdMutation();

  function adaptImages(images: Image[]) {
    let galleryImages: string[] = [];

    if (images.length === 0) {
      galleryImages.push("");
      return galleryImages;
    }

    galleryImages = images.reduce((result: string[], obj) => {
      if (Array.isArray(obj.tour_images)) {
        return result.concat(obj.tour_images);
      } else if (typeof obj.tour_images === 'string') {
        return result.concat(obj.tour_images);
      }
      return result;
    }, []);

    return galleryImages;
  }

  const galleryImages: string[] = adaptImages(tour.images || []);

  const IMAGEs: ListingGalleryImage[] = [...galleryImages].map(
    (item, index): ListingGalleryImage => {
      return {
        id: index,
        url: item,
      };
    }
  );

  useEffect(() => {
    getTour(id).unwrap().then((res) => {
      setTour(res);
    });
  }, [id]);



  return (
    <div className="ListingDetailPage">
      <ListingImageGallery
        isShowModal={modal === "PHOTO_TOUR_SCROLLABLE"}
        onClose={handleCloseModalImageGallery}
        images={IMAGEs}
      />

      <div className="container ListingDetailPage__content">{children}</div>

      {/* OTHER SECTION */}
      <div className="container py-24 lg:py-32">
        <div className="relative py-16">
          <BackgroundSection />
          <SectionSliderNewCategories
            heading="Explore by types of stays"
            subHeading="Explore houses based on 10 types of stays"
            categoryCardType="card5"
            itemPerRow={5}
            sliderStyle="style2"
            uniqueClassName="ListingDetailPage"
          />
        </div>
        <SectionSubscribe2 className="pt-24 lg:pt-32" />
      </div>

      {/* STICKY FOOTER MOBILE */}
      <MobileFooterSticky />
    </div>
  );
};

export default DetailPagetLayout;
